
import { defineComponent } from "vue";
import HtsTemplate from "@/apps/HTS/views/reports/templates/DataTableTemplate.vue";
import { ReportTemplateParams } from "@/apps/HTS/types/ReportTypes";
import { HtsReportService } from "@/apps/HTS/services/hts_report_service";
import { sort } from "fast-sort";

export default defineComponent({
  components: { HtsTemplate },
  setup() {
    const report = new HtsReportService("HTS SELF");
    const params: ReportTemplateParams = {
      logo: "/login-logos/PEPFAR.png",
      title: "HTS_SELF",
      drillDownLabelPattern: (label: string, data: any) => `${data.age_group} ${data.gender} ${label}`,
      initialSort: (data: any) => sort(data).asc((d: any) => d.gender),
      maxDate: HtsReportService.getSessionDate(),
      columns: [
        [
          {
            label: "Age group",
            value: (d: any) => d.age_group,
          },
          {
            label: "Gender",
            value: (d: any) => d.gender,
          },
          {
            label: "Directly-Assisted",
            columnType: 'drilldown',
            value: (d: any) => d.directly_assisted,
          },
          {
            label: "Unassisted",
            columnType: 'drilldown',
            value: (d: any) => d.unassisted,
          },
          {
            label: "Self",
            columnType: 'drilldown',
            value: (d: any) => d.self_recipient,
          },
          {
            label: "Sex Partner",
            columnType: 'drilldown',
            value: (d: any) => d.sex_partner,
          },
          {
            label: "Other",
            columnType: 'drilldown',
            value: (d: any) => d.other,
          }
        ]
      ],
      hasPeriodSelection: true,
      data: (rowData: any, formData: any, computedData: any) => {
        report.startDate = computedData.start_date;
        report.endDate = computedData.end_date;
        return report.requestReport();
      }
    };
    return { params };
  },
});
